.glow-border {
    padding: 35px;
    border: 1px none lightblue;
    border-radius: 15px;
    box-shadow: 0 0 10px lightgray;
}

.design-common {
    font-size: 14px;
    line-height: 22px;
    color: #000716;
    font-weight: 400;
    font-family: var(--font-family-base-qnistn, "Open Sans", "Helvetica Neue", Roboto, Arial, sans-serif);
    padding: 4px 12px;
    border-spacing: 0;
}

.tel-selector-left {
    border-radius: 8px;
    border-top: 2px solid #9ba7b6;
    border-bottom: 2px solid #9ba7b6;
    border-left: 2px solid #9ba7b6;
}

.tel-selector-right {
    border-radius: 8px;
    border-top: 2px solid #9ba7b6;
    border-bottom: 2px solid #9ba7b6;
    border-right: 2px solid #9ba7b6;
    width: 100%;
}

